import { Component, QueryList, ViewChild, ViewChildren, ViewContainerRef } from '@angular/core';

import {TemplateRefBinderDirective,TemplateProviderService, ALERT_CLASS, ModalConfig,ModalService } 
from '@ksaleh-ng/modal';


import { faSave } from '@fortawesome/free-solid-svg-icons';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'ksBlog';
  @ViewChild('msgBox', { read: ViewContainerRef })  msgBox : ViewContainerRef
  @ViewChildren(TemplateRefBinderDirective) templateRefs : QueryList<TemplateRefBinderDirective>;

constructor(
      private templateProviderService:TemplateProviderService,
          private modalSerivce:ModalService
){

}
ngAfterViewInit() {
  this.templateProviderService.Add(this.templateRefs.toArray());
}


OnMsgBox($event){

  let c = new ModalConfig();
      c.Width = 400;
      c.HasCaption =true;
      c.Title = "Hello World";
      //c.Body = "This Angular 9";
      c.TempalteName = "BeeGees";
      c.BindingContext = {BandName : "BeeGees"/*,Icon:faSave*/};
      //c.Alert = ALERT_CLASS.Success ;
      c.Buttons =[    {Text:'Yes',Code:0}, {Text:'No',Code:2}     ];

      c.ViewContainer = this.msgBox;
      
      let subscribtion2 = this.modalSerivce.ShowModal(c).Events.subscribe(
        code=>{
          alert(`user clicked buuton with code ${code}`);
            subscribtion2.unsubscribe(); 
        }
      );
  }



}

